<template>
  <div id="app">
    <div class="banner">
      <img src="./assets/banner.png" alt="">
    </div>
    <div class="mainBox">
      <div id="nav" class="nav">
        <div style="display: block;float: left;width: 27.3%;margin: 0 3%;line-height: 30px;color: #333;text-decoration: none;"
          :class="num == index? 'li_cur':''" 
          v-for="(item,index) of menuList" 
          :key="index" 
          @click="click_li(index)">
          <router-link class="navBtn" style="width:100%;" :to="item.linkto">{{item.name}}</router-link>
        </div>
      </div>
      <router-view/>
    </div>
    <!-- 为武汉点赞 -->
    <div class="Offering ">
      <div class="fabulous" style="width:29%;float:left;" @click="shareHandleClick">
        <img :src="shareImg" alt="">
        分享
      </div>
      <div class="fabulous" @click="fabulousClick()">
        <img :src="loveImg" alt="">
        武汉加油 {{count}} 次
      </div>
    </div>
    <!-- 分享提示 -->
    <div class="shareit" v-show="shareFlag" @click="shareHandleClick">
      <img class="arrow" src="./assets/cover.png">
    </div>
  </div>
</template>
<script>
export default {
  name: 'App',
  data () {
    return {
      flag: false,
      count: 0,
      li_cur: 'li_cur',
      num : 0,
      menuList: [
        {id: 1, name: '地图说疫情', linkto: '/Maptrend'},
        {id: 2, name: '实时疫情', linkto: '/RealTimeRpidemic'},
        {id: 3, name: '便民工具', linkto: '/ConvenienceTools'},
        // {id: 3, name: '在线义诊', linkto: '/OnlineClinic'},
        // {id: 4, name: '实时疫情', linkto: '/RealEpidemic'},
        // {id: 5, name: '动态推演', linkto: '/About'}
      ],
      loveImg: require('./assets/love.png'),
      shareImg: require('./assets/share.png'),
      shareFlag: false
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.getNum()
      this.$router.push({path:'/Maptrend'})
    },
    click_li (index) {
      this.num = index
    },
    fabulousClick() {
      const _this = this
      if(_this.flag){
        console.log('已经点过赞啦！')
        // alert('已经点过赞啦！')
        _this.$message({
          message: '已经点过赞啦！',
          type: 'warn',
        });
        return
      }
      this.$axios.post('/api/EncourageTown/update')
      .then(function (data) {
        data = data.data.result
        console.log(data)
        _this.count++
        _this.flag = true
        _this.loveImg = require('./assets/love2.png')
      })
    },
    getNum(){
      const _this = this
      this.$axios.get('/api/EncourageTown/findInfo')
      .then(function (data) {
        data = data.data.result
        _this.count = data.encourageCount
      })
    },
    shareHandleClick(){
      this.shareFlag = !this.shareFlag
    }
  }
}
</script>
