<template>
  <div class="MaptrendDiv">
    <h2 style="margin-top: 10px;">疫情扩散分布图</h2>
    <button class="trendBtn"  v-if="level == 1"
      :style="{background:bg?'#f45d23':'#339e07'}"
      @click="trendBtnClick()">动态推演</button>
    <button class="trendBtn" v-if="level > 1" 
      style="background:#f45d23;"
      @click="goBackProeMap()">全国地图</button>
    <div class="chinaecharts">
      <div id="mapChart2" ref="mapChart" class="chart" ></div>
    </div>
    <!-- 折线图 -->
    <div class="chinaecharts" style="height:250px;">
      <div id="lineChart" ref="lineChart" class="chart" ></div>
    </div>
    <div class="chinaecharts" style="height:250px;">
      <div id="lineChart2" ref="lineChart" class="chart" ></div>
    </div>

  </div>
</template>

<script>
var r = require.context('echarts/map/js/province')
r.keys().forEach(r)
export default {
	name: 'Maptrend',
  components: {
  },
  data () {
    return {
      zhiyu:'',
      prevZhiyu:'',
      siwang:'',
      prevsiwang:'',
      yisi:'',
      prevyisi:'',
      quezhen:'',
      prevquezhen:'',

      aaa: {}, // 动态数据模拟
      bbb: {},
      index: 0,
      timer: null,
      shengData:[],
      shiData:[],
      chartDatas:[],
      mapChart: {},
      lineChart2: {},
      lineChart3: {},
      bg: false,
      // 折线图数据
      line_xAxis: [],
      line_data1:[],
      line_data2:[],
      level: 1,
      citysData: []
    }
  },
  watch: {
    index () {
      if (this.index >= this.chartDatas.length) {
        clearInterval(this.timer)
        this.clickMap('',this.citysData)
        this.timer = null
        this.bg = false
        this.index = 0
      }
    },
    citysData () {
      const _this = this
      let arr = []
      for(let i=0;i<_this.citysData.length;i++){
        let item = _this.citysData[i]
        let shiData = []
        for(let j=0;j<item.citys2.length;j++){
          let item2 = item.citys2[j]
          let childObj = {}
          childObj.name = item2.townName
          childObj.value = item2.currentStatistics.confirmedCount == null ? 0: item2.currentStatistics.confirmedCount
          childObj.confirmedCount = item2.currentStatistics.confirmedCount == null ? 0: item2.currentStatistics.confirmedCount
          childObj.curedCount = item2.currentStatistics.curedCount == null ? 0: item2.currentStatistics.curedCount
          childObj.deadCount = item2.currentStatistics.deadCount == null ? 0: item2.currentStatistics.deadCount
          childObj.suspectedCount = item2.currentStatistics.suspectedCount == null ? 0: item2.currentStatistics.suspectedCount
          shiData.push(childObj)
        }
        item.citys = shiData
        arr.push(item)
      }
      // console.log(arr)
      _this.citysData = arr
    }
  },
  mounted () {
    this.init()
  },
  destroyed () {
    clearInterval(this.timer)
  },
  beforeDestroy () {
    this.mapChart.clear()
    this.lineChart2.clear()
    this.lineChart3.clear()
  },
  methods: {
    init(){
      let lineChart = this.$echarts.init(document.getElementById('lineChart'));
      this.lineChart2 = lineChart
      lineChart = this.$echarts.init(document.getElementById('lineChart2'));
      this.lineChart3 = lineChart
      this.getDatas()
      this.getFindCitys()
    },
    goBackProeMap(){
      const _this = this
      $(".retPro").hide()
      // this.mapFn();
      _this.level = 1
      _this.clickMap('',_this.citysData)
    },
    // 合并数组，将两个一维数组合并成一个二维数组
    arrangementData (arr1, arr2) {
      let s = arr1.reduce((a, item) => {  
      let obj = {  
        ...item,  
        data: []  
      }  
      arr2.forEach(i => i.cid === item.id && obj.data.push(i))  
      a.push(obj)  
        return a  
      }, [])  
      return s
    },
    dateFormat(fmt, date) {
      let ret;
      const opt = {
        "Y+": date.getFullYear().toString(),        // 年
        "m+": (date.getMonth() + 1).toString(),     // 月
        "d+": date.getDate().toString(),            // 日
        "H+": date.getHours().toString(),           // 时
        "M+": date.getMinutes().toString(),         // 分
        "S+": date.getSeconds().toString()          // 秒
        // 有其他格式化字符需求可以继续添加，必须转化成字符串
      };
      for (let k in opt) {
        ret = new RegExp("(" + k + ")").exec(fmt);
        if (ret) {
            fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
        };
      };
      return fmt;
    },
    // 获取两个日期间的日期数组
    getdiffdate (stime,date) {
      let etime = this.dateFormat('YYYY-mm-dd HH:MM',date)
      // let etime = new Date().toLocaleDateString()
      //初始化日期列表，数组
      let diffdate = new Array();
      let i=0;
      let date_arr = []
      //开始日期小于等于结束日期,并循环
      while(stime<=etime){
        diffdate[i] = stime;
        //获取开始日期时间戳
        let stime_ts = new Date(stime).getTime();
        // console.log('当前日期：'+stime   +'当前时间戳：'+stime_ts);
        date_arr.push(stime)
        //增加一天时间戳后的日期
        let next_date = stime_ts + (24*60*60*1000);
        //拼接年月日，这里的月份会返回（0-11），所以要+1
        let next_dates_y = new Date(next_date).getFullYear()+'-';
        let next_dates_m = (new Date(next_date).getMonth()+1 < 10)?'0'+(new Date(next_date).getMonth()+1)+'-':(new Date(next_date).getMonth()+1)+'-';
        let next_dates_d = (new Date(next_date).getDate() < 10)?'0'+new Date(next_date).getDate():new Date(next_date).getDate();
        stime = next_dates_y+next_dates_m+next_dates_d;
        //增加数组key
        i++;
      }
      return date_arr
    },
    // 获取地图数据
    getDatas(){
      const _this = this
      this.$axios.get('/api/gmtown/findHistory')
      .then(function (data) {
        // let shengData = []
        // let shiData = []
        data = data.data.result
        // console.log(data)
        let lineData = []
        for(let key  in data){
          let dataChartObj = {}
          dataChartObj.date = key
          // dataChartObj.data_arr = data[key]
          // lineData.push(dataChartObj)
          let line_arr = []
          let sum1 = 0
          let sum2 = 0
          let sum3 = 0
          let sum4 = 0
          let data2 = data[key]
          for(let i=0;i<data2.length;i++){
            let item = data2[i]
            // data2.map(item => {
            let object = {} 
            if(item.townName.includes('内蒙古')){
              object.name = '内蒙古'
            }else if(item.townName.includes('自治区')) {
              let length = item.townName.length
              object.name = item.townName.slice(0,2)
            }else{
              let length = item.townName.length
              object.name = item.townName.slice(0,length - 1).trim()
            }
            object.value = item.currentStatistics.confirmedCount == null ? 0: item.currentStatistics.confirmedCount
            object.confirmedCount = item.currentStatistics.confirmedCount == null ? 0: item.currentStatistics.confirmedCount
            object.curedCount = item.currentStatistics.curedCount == null ? 0: item.currentStatistics.curedCount
            object.deadCount = item.currentStatistics.deadCount == null ? 0: item.currentStatistics.deadCount
            object.suspectedCount = item.currentStatistics.suspectedCount == null ? 0: item.currentStatistics.suspectedCount
            sum1 += item.currentStatistics.confirmedCount
            sum2 += item.currentStatistics.curedCount
            sum3 += item.currentStatistics.deadCount
            sum4 += item.currentStatistics.suspectedCount
            line_arr.push(object)
          //   return item
          // })
          }
          dataChartObj.sum1 = sum1
          dataChartObj.sum2 = sum2
          dataChartObj.sum3 = sum3
          dataChartObj.sum4 = sum4
          dataChartObj.data_arr = [...line_arr]
          lineData.push(dataChartObj)
        }

        // console.log(lineData)
        _this.chartDatas = lineData
        // _this.timer = setInterval(_this.handleMap, 1000);
        // let index = lineData.length
        // _this.clickMap(lineData[index - 1].date,lineData[index - 1].data_arr)
        //
        let xAxisData = lineData.map(item => {
          return item.date
        })
        let sum_arr1 = lineData.map(item => {
          return item.sum1
        })
        let sum_arr2 = lineData.map(item => {
          return item.sum2
        })
        let sum_arr3 = lineData.map(item => {
          return item.sum3
        })
        let sum_arr4 = lineData.map(item => {
          return item.sum4
        })
        let seriesData = [sum_arr1,sum_arr2,sum_arr3,sum_arr4]
        _this.lineChart(_this.lineChart2,xAxisData,seriesData)
        _this.lineChart(_this.lineChart3,xAxisData,seriesData)
      })
      .catch(function (error) {
        console.log(error)
      })    
    },
    trendBtnClick(){
      const _this = this
      if(_this.bg){
        // alert('推演中！')
        _this.$message({
          message: '推演中！',
          type: 'warn',
        });
        return
      }
      _this.bg = true
      let value = _this.chartDatas
      _this.timer = setInterval(_this.handleMap, 1000);
    },
    handleMap(){
      const _this = this
      let value = _this.chartDatas
      _this.clickMap(value[_this.index].date,value[_this.index].data_arr)
      _this.index++;
    },
    // 睡眠函数
    sleep(numberMillis){
      let now = new Date()
      let exitTime = now.getTime() + numberMillis
      while(true){
        now = new Date()
        if(now.getTime() > exitTime){
          return
        }
      }
    },
    getFindCitys(){
      const _this = this
      this.$axios.get('/api/gmtown/findCitys')
      .then(function (data) {
        let citysData = []
        data = data.data.result
        for(let i=0;i<data.length;i++){
          let item = data[i]
        // data.map(item => {
          let object = {}
          if(item.townName.includes('内蒙古')){
            object.name = '内蒙古'
          }else if(item.townName.includes('自治区')) {
            let length = item.townName.length
            object.name = item.townName.slice(0,2)
          }else{
            let length = item.townName.length
            object.name = item.townName.slice(0,length - 1)
          }
          object.value = item.currentStatistics.confirmedCount == null ? 0: item.currentStatistics.confirmedCount
          object.confirmedCount = item.currentStatistics.confirmedCount == null ? 0: item.currentStatistics.confirmedCount
          object.curedCount = item.currentStatistics.curedCount == null ? 0: item.currentStatistics.curedCount
          object.deadCount = item.currentStatistics.deadCount == null ? 0: item.currentStatistics.deadCount
          object.suspectedCount = item.currentStatistics.suspectedCount == null ? 0: item.currentStatistics.suspectedCount
          object.citys2 = item.citys
          citysData.push(object)
        //   return item
        // })
        }
        _this.citysData = citysData
        _this.clickMap('',citysData)
      })
      .catch(function (error) {
        console.log(error)
      })    
    },
    // 判断相同字符
    fiterStr(str1,str2){
      let l = str1.length > str2.length ? str2.length : str1.length //取短的
      let i = 0
      while(i < l){
        if(str1[i] !== str2[i])break
        i++
      }
      return i
    },
    clickMap(date,mapData) {
      const _this = this
      // 基于准备好的dom，初始化echarts实例
      // var mapChart = this.$echarts.init(this.$refs.mapChart);
      var mapChart = this.$echarts.init(document.getElementById('mapChart2'));
      _this.mapChart = mapChart
      // console.log(mapData)
      let option = _this.getOption(date,mapData)
      // mapChart.setOption(option)
      mapChart.setOption(option, true, false)
      _this.mapChart.on('click', function (result) {
        if(_this.bg){
          _this.$message({
            message: '推演中,请稍后！',
            type: 'warn',
          });
          return
        }
        $(".retPro").show()
        // alert(_this.level)
        if(_this.level >= 2){
          return
        }
        for(let i=0;i<mapData.length;i++){
          let item = mapData[i]
          let name_str = item.name.slice(0,2)
          if(result.name.includes(name_str)){
            console.log(item.citys)
            option = _this.getOption('',item.citys)
            option.title.text = result.name
            option.series[0].map = result.name
            _this.mapChart.setOption(option, true, false);
            _this.level++
          }
        }
        // option = _this.getOption(item.citys)
        // option.title.text = result.name
        // option.series[0].map = result.name
        // _this.mapChart.setOption(option, true, false);
        // _this.level++
      })
      window.addEventListener('resize', () => {
          // 自动渲染echarts
          mapChart.resize();
      })
    },
    // 地图charts
    getOption(date,mapData) {
      // console.log(mapData)
      const _this = this
      let option = {
        backgroundColor: '', //背景颜色
        title: {
            text: '中国',
            subtext: date,
            color: '#fff',
            x:'center',
            top: '50px'
				},
				// grid: {
				// 	top: '75%',
				// 	bottom:'5%', 
				// 	left: '40%',
				// 	right: '5%',
        //   //坐标轴距离上下左右的百分比
        //   opacity: '0.1'   //透明度
        // },
        //是视觉映射组件，用于进行『视觉编码』，也就是将数据映射到视觉元素（视觉通道）。
        visualMap: {
          // 区间展示
          type: 'piecewise',
          // inverse: true,
          left: 10,
          bottom: 10,
          pieces: [ 
            {gte: 1000,color: '#75141d'},
            {gte: 500, lt: 1000,color: '#c92d30'},
            {gte: 100, lt: 499,color: '#e5624f'},
            {gte: 10, lt: 99,color: '#f2a88d'},
            {gte: 1, lt: 9,color: '#fceed3'},
            {value: 0,color: '#f5f5f5'}
          ],
          // pieces: [ 
          //   {gte: 1000,color: '#75141d'},
          //   {gte: 500, lt: 1000,color: '#c92d30'},
          //   {gte: 100, lt: 499,color: '#e58b4f'},
          //   {gte: 10, lt: 99,color: '#eba83f'},
          //   {gte: 1, lt: 9,color: '#e8c960'},
          //   {value: 0,color: '#e8e360'}
          // ],
          itemGap:5, // 两个图元之间的间距
        },
        // 提示框，鼠标移入
        tooltip:{
            show:true, //鼠标移入是否触发数据
            trigger: 'item', //出发方式
            // formatter:'{b}<br />确诊量：{c}'
            formatter:function(value){
              let str = value.name
              for(let i=0;i<mapData.length;i++){
                if(mapData[i].name.includes(value.name)){
                  str = `${str}<br />确诊：${mapData[i].confirmedCount}
                    <br />治愈：${mapData[i].curedCount}<br />疑似：${mapData[i].suspectedCount}
                    <br />死亡：${mapData[i].deadCount}`
                }
              }
              return str
            }
        },
        series:[
            {
                name:'地图',
                type: 'map',  //地图种类
                map: 'china', //地图类型。
                data: mapData,
                itemStyle: { //地图区域的多边形 图形样式。
                    emphasis:{ //高亮状态下的样试
                        label:{
                            show:true,
                        }
                    }
                },
                zoom:1.2,//放大比例
                label: {  //图形上的文本标签，可用于说明图形的一些数据信息
                    show:true,
                },
            },
            {
                type:'scatter',
                showEffectOn: 'render',//配置什么时候显示特效
                coordinateSystem:'geo',//该系列使用的坐标系
                symbolSize:10,//标记的大小
                zlevel:99999
            }
        ]
      }
      return option
    },
    lineChart(lineDome,xAxisData,seriesData){
      const _this = this
      let option = {
        title: {
          text: '全国疫情统计',
          // subtext: 'China',
          color: '#fff',
          x:'center',
          top: 0
				},
        legend: {
          data:['确诊量', '治愈量', '死亡量', '疑似量'],
          top: 30,
          left:'center',
          textStyle:{
            color:'#000',
          }
        },
        grid: {
          top: 66,
          bottom: 25, 
          left: 50,
          right: 30,
          //坐标轴距离上下左右的百分比
          opacity: '0.1'   //透明度
        },
        xAxis: {
          type: 'category',
          data: xAxisData
          // data: _this.getdiffdate('2020-01-20',new Date())
          // data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
        },
        yAxis: {
          type: 'value'
        },
        series: [{
          name: '确诊量',
          data: seriesData[0],
          // data: [820, 932, 901, 934, 1290, 1330, 1320, 932, 901, 934, 1290, 1330, 1320,820, 932, 901, 934],
          type: 'line',
          // smooth: true
          },{
            name: '治愈量',
            data: seriesData[1],
            // data: [120, 922, 801, 834, 990, 1130, 1120, 922, 801, 834, 990, 1130, 1120,834, 990, 1130, 1120],
            type: 'line',
            // smooth: true
          },{
            name: '死亡量',
            data: seriesData[2],
            type: 'line',
            // smooth: true
          },{
            name: '疑似量',
            data: seriesData[3],
            type: 'line',
            // smooth: true
          }
        ]
      }
      lineDome.setOption(option, true, false)
      // let lineChart = this.$echarts.init(document.getElementById('lineChart'));
      // _this.lineChart2 = lineChart
      // lineChart.setOption(option, true, false)
      // window.addEventListener('resize', () => {
      //     // 自动渲染echarts
      //     lineChart.resize();
      // })
      //
      // let count = 11;
      // _this.bbb = setInterval(function (){
      //     var axisData = (new Date()).toLocaleTimeString().replace(/^\D*/, '');

      //     var data0 = option.series[0].data;
      //     var data1 = option.series[1].data;
      //     data0.shift();
      //     data0.push(Math.round(Math.random() * 1000));
      //     data1.shift();
      //     data1.push(Math.round(Math.random()*1000));

      //     option.xAxis.data.shift();
      //     option.xAxis.data.push(count++);

      //     lineChart.setOption(option);
      // }, 2100);
    }
    //
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus">
.MaptrendDiv{
  height:calc(100% - 30px);
  overflow:auto;
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.numberLit{
  height: 60px;
}
.numberLit li{
  width: 20%;
  float: left;
  background: #efefef;
  border-radius: 5px;
  margin: 10px 2% 0 1%;
}
.numberLit li:nth-child(1){
  margin: 10px 2% 0 5%;
}
.numberLit li a{
  display: block;
  color: #171717;
  text-align: center;
  font-size: 1.2rem;
  margin-top: 8px;
}
.numberLit li b{
  display: block;
  text-align: center;
  font-size: 1.8rem;
}
.numberLit li p{
  color: #999999;
  text-align: center;
  font-size: 0.7rem;
  margin-bottom: 8px;
}
.numberLit li.green b,
.numberLit li.green p span{
  color: #2ec89b;
}
.numberLit li.black b,
.numberLit li.black p span{
  color: #52436b;
}
.numberLit li.yellow b,
.numberLit li.yellow p span{
  color: #f78a16;
}
.numberLit li.blue b,
.numberLit li.blue p span{
  color: #157fc9;
}
.chinaecharts {
    width: 100%;
    height: 400px;
}
.chart {
    width: 100%;
    height: 100%;
}
.trendBtn{
  float: left;
  position: relative;
  top: 20px;
  left: 20px;
  background: #f45d23;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  color: #fff;
  outline: none;
  z-index: 9999;
}
.retPro{z-index:999;position: absolute;top:45%;left:10px; display: none;}
.retPro a{height:30px;line-height: 30px;border:1px solid #ccc;padding:5px 10px;margin-left:20px;border-radius:5px;cursor: pointer;}
</style>
